.slick-arrow {
  display: none !important;
}
.slick_carousel {
  background-color: #ffffff;
  text-align: center;
  padding: 20px 0;
  height: 60px;
  cursor: pointer;
  img {
    width: 32px;
    margin: auto;
  }
  p {
    height: 20px;
    margin: 10px 0 0 0;
    font-size: 13px;
  }
}
